import React, { useState } from "react";
import Modal from "react-modal";
import CloseIcon from '@mui/icons-material/Close';
Modal.setAppElement("body"); 

function EventModal(props){

    //state
    const [title, setTitle] = useState('');
    //const [start, setStart] = useState(props.clickday);
    const [end, setEnd] = useState('');

    function handleModalClose(){
        props.closeModal();
    }

    function SubmitHandler(e){
        e.preventDefault();
        if(title === ''){
            window.alert('タイトルを入力してください');
        } else if (end === ''){
            window.alert('終了日を入力してください');
        } else {
            props.addEvent(title,end);
            setTitle('');
            //setStart('');
            setEnd('');
            props.closeModal();
        }
    }

    return (
        <Modal
        className="Modal"
        overlayClassName="OverlayModal"
        isOpen={props.modal}
        onRequestClose={handleModalClose}
        >
        <button
        className="ModalCloseButton"
        onClick={handleModalClose}
        >
        <CloseIcon/>
        </button>
        <h2 className="ModalTitle">予定を入れろよ</h2>
        <hr />
        <div className="EventAddForm">
        <form >
            <div className="FormItem">
                <label htmlFor="Title">タイトル</label>
                <input
                id="Title"
                type="text"
                onChange={(e) => setTitle(e.target.value)}
                />
            </div>
            <div className="FormItem">
                <label htmlFor="Start">開始日</label>
                <span id="Start">{props.clickday}</span>
            </div>
            <div className="FormItem">
                <label htmlFor="End">終了日</label>
                <input
                id="End"
                type="date"
                onChange={(e) => {setEnd(e.target.value)}}
                />
            </div>
            <div className="FormItem">
                <button onClick={SubmitHandler}>submit</button>
            </div>
        </form>
        </div>
        </Modal>
    );
}

export default EventModal;