import React, { useState } from 'react';
import Calender from "./components/Calender";
import EventModal from "./components/EventModal";
import DeleteEditModal from "./components/DeleteEditModal"
import BulkRegistation from './components/BulkRegistation';
import { nanoid } from "nanoid";


function App() {
  //state
  const [ modal, setModal] = useState(false);
  const [ deletEditModal, setDeleteEditModal] = useState(false);
  const [ bulkResistationModal,setBulkResistationModal ] = useState(false);
  const [ events, setEvents] = useState([]);
  const [ clickday, setClickday] = useState('');
  const [ clickevent, setClickevent] = useState('');


  function showModal(){
    setModal(true);
  }

  function closeModal(){
    setModal(false);
  }

  function showDeleteEditModal(){
    setDeleteEditModal(true);
  }

  function closeDeleteEditModal(){
    setDeleteEditModal(false);
  }

  function dateClickHandler(info){
    showModal();
    setClickday(info.dateStr);
  }

  function eventClickHandler(info){
      showDeleteEditModal();
      setClickevent(info.event);
  }

  function ClickBulkRegistationHandler(){
    setBulkResistationModal(true);
  }

  function closeBulkRegistationHandler(){
    setBulkResistationModal(false);
  }

  function addEvent(title,end){
    const newEvent = {id: `event-${nanoid()}`,title:title, start:clickday, end:end, color:"seagreen"}
    setEvents([...events, newEvent]);
    setClickday('');
  }

  function deleteEvent(){
    const newEvents = events.filter((event) =>
    {
      return event.id !== clickevent.id;
    });
    setEvents(newEvents);
    setClickevent('');
  }

  function editEvent(editTitle,editStart,editEnd){
    console.log(editTitle);
    console.log(editStart);
    console.log(editEnd);
    const editEventsList = events.map((event) => {
      if(event.id === clickevent.id){
          return {...event, title:editTitle, start:editStart, end:editEnd};
        }
      return event;
    });
    setEvents(editEventsList);
    setClickevent('');
  }

  function bulkRegistasionEvents(bulkEventsTitle,bulkEvents){
      console.log(bulkEventsTitle);
      console.log(bulkEvents);
      //const newbulkEvents = [];

      const newbulkEvents = bulkEvents.map((bulkEvent) => (
        {id: `event-${nanoid()}`,title:bulkEventsTitle, start:bulkEvent.start, end:bulkEvent.end, color:"seagreen"}
      ));
      /*
      bulkEvents.forEach((bulkEvent) => {
        const newbulkEvent = {id: `event-${nanoid()}`,title:bulkEventsTitle, start:bulkEvent.start, end:bulkEvent.end, color:"seagreen"};
        newbulkEvents.push(newbulkEvent);
      })
      */
      console.log(newbulkEvents);
      setEvents([...events, ...newbulkEvents]);
  }

  return (
    <div>
      <div className='calender'>
        <Calender events={events} dateClickHandler={dateClickHandler} eventClickHandler={eventClickHandler} ClickBulkRegistationHandler={ClickBulkRegistationHandler}/>
      </div>
      <div className='modal'>
        <EventModal modal={modal} closeModal={closeModal} addEvent={addEvent} clickday={clickday}/>
      </div>
      <div className='modal'>
        <DeleteEditModal deletEditModal={deletEditModal} closeDeleteEditModal={closeDeleteEditModal} clickevent={clickevent} deleteEvent={deleteEvent} editEvent={editEvent}/>
      </div>
      <div className='modal'>
        <BulkRegistation bulkResistationModal={bulkResistationModal} closeBulkRegistationHandler={closeBulkRegistationHandler} bulkRegistasionEvents={bulkRegistasionEvents}/>
      </div>
    </div>
  );
}

export default App;
