import React,{useState} from "react";
import Modal from "react-modal";
import CloseIcon from '@mui/icons-material/Close';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list'; 
import interactionPlugin from '@fullcalendar/interaction'; 

Modal.setAppElement("body"); 

function BulkRegistation(props){
    
    //state
    const [bulkEvents,setbulkEvents] = useState([]);
    const [bulkEventsTitle, setBulkEventsTitle] = useState('');
    const myPlugin = [dayGridPlugin, timeGridPlugin, listPlugin, interactionPlugin];

    const list = bulkEvents.map((bulkEvent) =>
        <li> {bulkEvent.start} </li>
    );

    function handleBulkResistationModalClose(){
        props.closeBulkRegistationHandler();
        setbulkEvents([]);
    }

    function dateClick(info){
        const newBulkEvent = {id:info.dateStr,start:info.dateStr,end:info.dateStr,display:'background',key:info.dateStr};
        let flg = 'T';
        let newBulkEvents = [];

        bulkEvents.forEach(bulkEvent => {
            if(bulkEvent.id === newBulkEvent.id){
                flg = 'F';
            } else {
                newBulkEvents.push(bulkEvent);
            }
        });
        if(flg === 'T'){
            setbulkEvents([...bulkEvents,newBulkEvent]);
        } else {
            setbulkEvents(newBulkEvents);
        }
    }

    function handleBulkResistationSubmit(e){
        e.preventDefault();
        
        if(bulkEventsTitle === ''){
            window.alert('タイトルを入力してください');
        } else if(bulkEvents.length === 0){
            window.alert('日付が選択されていません');
        } else {
            props.bulkRegistasionEvents(bulkEventsTitle,bulkEvents);
            setbulkEvents([]);
            props.closeBulkRegistationHandler();
        }
    }

    return(
        <Modal
        className="Modal"
        overlayClassName="OverlayModal"
        isOpen={props.bulkResistationModal}
        onRequestClose={handleBulkResistationModalClose}
        >
        <button
        className="ModalCloseButton"
        onClick={handleBulkResistationModalClose}
        >
        <CloseIcon/>
        </button>
        <h2 className="ModalTitle">予定を一括登録</h2>
        <hr />
        <div className="BulkRegistationForm">
        <form>
            <div className="FormItem">
                <label htmlFor="Title">タイトル</label>
                <input 
                id="title"
                type="text"
                onChange={(e) => setBulkEventsTitle(e.target.value)} />
             <div className="FormItem">
                <button onClick={handleBulkResistationSubmit}>submit</button>
            </div>
            </div>
        </form>
        </div>
        <div>
            <p className="daylisttitle"> ▶︎ 選択した日付</p>
            <ul className="daylist">{list}</ul>
        </div>
        <div className="modalCalendar">
        <FullCalendar
        events={bulkEvents}
        plugins={myPlugin}
        initialView="dayGridMonth"
        contentHeight = "auto"
        timeZone = "Asia/Tokyo"
        dateClick = {dateClick}
        />
        </div>
        </Modal>
    );
}

export default BulkRegistation;