import React from "react";
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list'; 
import interactionPlugin from '@fullcalendar/interaction'; 
//import jaLocale from '@fullcalendar/core/locales/ja';

function Calender(props){

    const myHeaderToolbar = {
        start: "prev,next today",
        center: "title",
        right: "customButton dayGridMonth,listMonth timeGridWeek,listWeek",
      };
    
    const myButtonText = {
        month:" 月 ",
        listMonth:"/予定一覧",
        week:" 週 ",
        listWeek:"/予定一覧"
    };
    
    const myPlugin = [dayGridPlugin, timeGridPlugin, listPlugin, interactionPlugin];

    const myCustomButton = {
        customButton:{
            text: '一括登録',
            click: props.ClickBulkRegistationHandler
            }
        }   

    return(

        <FullCalendar
        plugins={myPlugin} 
        initialView="dayGridMonth"
        headerToolbar={myHeaderToolbar}
        events={props.events}
        eventDisplay={'block'}
        buttonText={myButtonText}
        customButtons= {myCustomButton}
        dateClick = {props.dateClickHandler}
        eventClick = {props.eventClickHandler}
        contentHeight = "auto"
        timeZone = "Asia/Tokyo"
        dayMaxEvents={1}
        />

    );
}

export default Calender;