import React,{useState} from "react";
import Modal from "react-modal";
import CloseIcon from '@mui/icons-material/Close';
Modal.setAppElement("body"); 

function DeleteEditModal(props){
    let title = '';
    let start = '';
    let end = '';
    
    //state
    const [isEditing,setEditing] = useState(false);
    const [editTitle, setEditTitle] = useState('');
    const [editStart, setEditStart] = useState('');
    const [editEnd, setEditEnd] = useState('');
    

    function handleDeleteEditModalClose(){
        props.closeDeleteEditModal();
    }

    function ClickDeleteEventHandler(){
        props.deleteEvent();
        props.closeDeleteEditModal();
    }

    function ClickEditEventHandler(){
        setEditing(true);
        props.closeDeleteEditModal();
    }

    function handleEditModalClose(){
        setEditing(false);
    }

    function SubmitEditHandler(e){
        e.preventDefault();

        if(editTitle !== '' && editStart !== '' && editEnd !== ''){
            props.editEvent(editTitle,editStart,editEnd);
        } else if( editTitle !== '' && editStart !== ''){
            props.editEvent(editTitle,editStart,end);
        } else if( editTitle !== '' && editEnd !== '' ){
            props.editEvent(editTitle,start,editEnd);
        } else if(editStart !== '' && editEnd !== '' ){
            props.editEvent(title,editStart,editEnd);
        } else if( editTitle !== ''){
            props.editEvent(editTitle,start,end);
        }  else if( editStart !== ''){
            props.editEvent(title,editStart,end);
        } else if( editEnd !== '') {
            props.editEvent(title,start,editEnd);
        } 

        setEditing(false);
        setEditTitle('');
        setEditStart('');
        setEditEnd('');
    }

    if(props.clickevent === null || props.clickevent === '' || typeof props.clickevent === "undefined"){
    //開始日と終了日が同日の予定を入れると、endがnullになるから判定しないとバグる。
    } else if (props.clickevent.end === null || props.clickevent.end === '' || typeof props.clickevent.end === "undefined" ){
        title = `${props.clickevent.title}`;
        start = `${props.clickevent.start.getFullYear()}-${(props.clickevent.start.getMonth()+1).toString().padStart(2, '0')}-${props.clickevent.start.getDate().toString().padStart(2, '0')}`;
        end = `${start}`;
    } else {
        title = `${props.clickevent.title}`;
        start = `${props.clickevent.start.getFullYear()}-${(props.clickevent.start.getMonth()+1).toString().padStart(2, '0')}-${props.clickevent.start.getDate().toString().padStart(2, '0')}`;
        end = `${props.clickevent.end.getFullYear()}-${(props.clickevent.end.getMonth()+1).toString().padStart(2, '0')}-${props.clickevent.end.getDate().toString().padStart(2, '0')}`;
    }

    const editFormTemplate = (
        <Modal
        className="Modal"
        overlayClassName="OverlayModal"
        isOpen={isEditing}
        onRequestClose={handleEditModalClose}
        >
        <button
        className="ModalCloseButton"
        onClick={handleEditModalClose}
        >
        <CloseIcon/>
        </button>
        <h2 className="ModalTitle">予定を編集しろよ</h2>
        <hr />
        <div className="editForm">
            <form>
                <div className="FormItem">
                <label htmlFor="Title">タイトル</label>
                <input
                id="Title"
                type="text"
                defaultValue={props.clickevent.title}
                onChange={(e)=>setEditTitle(e.target.value)}/>
                </div>
                <div className="FormItem">
                <label htmlFor="Start">開始日</label>
                <input
                id="Start"
                type="date"
                defaultValue={start}
                onChange={(e)=>setEditStart(e.target.value)}/>
                </div>
                <div className="FormItem">
                <label htmlFor="End">終了日</label>
                <input
                id="End"
                type="date"
                defaultValue={end}
                onChange={(e)=>setEditEnd(e.target.value)}/>
                </div>
                <div className="FormItem">
                <button onClick={SubmitEditHandler}>submit</button>
                </div>
            </form>
        </div>
        </Modal>
    );

    const ModalTenplate = (
        <Modal
        className="Modal"
        overlayClassName="OverlayModal"
        isOpen={props.deletEditModal}
        onRequestClose={handleDeleteEditModalClose}
        >
        <button
        className="ModalCloseButton"
        onClick={handleDeleteEditModalClose}
        >
        <CloseIcon/>
        </button>
        <div className="eventView">
            <div className="eventViewItem">
                <label htmlFor="eventViewId"></label>
                <h2 id="eventViewId">{title}</h2>
                <hr />
            </div>
            <div className="eventViewItem">
                <label htmlFor="eventViewStart"></label>
                <span  id="eventViewStart">{`開始日 : ${start}`}</span>
            </div>
            <div className="eventViewItem">
                <label htmlFor="eventViewEnd"></label>
                <span id="eventViewEnd">{`終了日 : ${end}`}</span>
            </div>
        </div>
        <div className="DeleteEditModal">
        <div className="deletebutton">
            <p>該当のイベントを削除</p>
            <button
            onClick={ClickDeleteEventHandler}
            >削除
            </button>
        </div>
        <div className="editbutton">
            <p>該当のイベントを編集</p>
            <button
            onClick={ClickEditEventHandler}>編集</button>
        </div>
        </div>
        </Modal>
    );

    return(
        <div>
            {isEditing ? editFormTemplate:ModalTenplate}
        </div>
    );
}

export default DeleteEditModal;